$(document).ready(function() {

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Select
    $(".custom_select select").niceSelect()

    // Accordion
    $(".accordion-show").click((function(){
        $(this).toggleClass("accordion-show--active")
        $(this).siblings(".accordion-hidden").slideToggle()
    }));

    // Sidebar
    $('.open-sidebar').click(function () {
        $('.sidebar').addClass('sidebar--active')
        $('.sidebar-bg').addClass('sidebar-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.sidebar__close').click(function () {
        $('.sidebar').removeClass('sidebar--active')
        $('.sidebar-bg').removeClass('sidebar-bg--active')
        $('body').removeClass('fixed-body')
    })

})
